<template>
  <div class="control_list">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>商品列表</span>
      </div>
      <div class="text item">
        <div control-table>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-form-item label="商品名称">
                    <span>{{ props.row.goodsname }}</span>
                  </el-form-item>
                  <el-form-item label="商品特色">
                    <span>{{ props.row.goodsfeature }}</span>
                  </el-form-item>
                  <el-form-item label="商品 ID">
                    <span>{{ props.row.id }}</span>
                  </el-form-item>
                  <el-form-item label="包装费">
                    <span>{{ props.row.packprice }}</span>
                  </el-form-item>
                  <el-form-item label="商品分类">
                    <span>{{ props.row.goodscategory }}</span>
                  </el-form-item>
                  <el-form-item label="商品价格">
                    <span>{{ props.row.price }}</span>
                  </el-form-item>
                  <el-form-item label="商品描述">
                    <span>{{ props.row.goodsdesc }}</span>
                  </el-form-item>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column label="商品 ID" prop="id"></el-table-column>
            <el-table-column label="商品名称" prop="goodsname"></el-table-column>
            <el-table-column label="商品图片">
              <template slot-scope="scope">
               <img :src="scope.row.imgUrl" width="50">
              </template>
            </el-table-column>
            <el-table-column label="描述" prop="goodsdesc"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)">修改</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block" style="float:left; margin:25px 0 30px 24px">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="pageSize"
              layout="total,prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from "axios";
export default {
  created() {
    this.getlist();
  },
  data() {
    return {
      tableData: [],
      total: 1,
      page: 1,
      pageSize:4,
      imgUrl:'',
    };
  },
  methods: {
    handleEdit(row) {
      // console.log(row);
      this.$router.push(`/index/control_update/${row.id}`);
    },
    handleDelete(row) {
      // console.log(row);
      axios
        .get(`http://127.0.0.1:3333/goods.php?a=delete&id=${row.id}`)
        .then(data => {
          // console.log(data)
          if (data.data.code == 0) {
            this.$message({
              message: data.data.msg,
              type: "success"
            });
            this.getlist();
            } else {
            this.$message.error(data.data.msg);
           
          }
        });
    },
    handleSizeChange(val) {
      // console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.getlist();
    },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`);
      this.page = val;
      this.getlist();
    },
    getlist() {
      axios
        .get(
          `http://127.0.0.1:3333/goods.php?a=list&currentPage=${this.page}&pageSize=${this.pageSize}`
        )
        .then(data => {
          // console.log(data);
          if (data.data.code == 0) {
            this.tableData = data.data.data;
            this.imgUrl=data.data.data.imgUrl
            this.total = parseInt(data.data.total);
          }
        });
    }
  }
};
</script>

<style >
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

.control_list .text {
  padding-top: 10px;
}
</style>